// Code block styles

// Inline code
code {
  font-family: 'JetBrains Mono', 'Fira Code', 'SF Mono', Monaco, 'Inconsolata', 'Fira Mono', monospace;
  font-size: 0.875em;
  background-color: var(--color-bg-tertiary);
  color: var(--color-primary-700);
  padding: 0.125em 0.375em;
  border-radius: $border-radius-sm;
  font-weight: map-get($font-weights, 'normal');
  
  [data-theme="dark"] & {
    color: var(--color-primary-400);
  }
}

// Code blocks
pre {
  position: relative;
  background-color: var(--color-gray-50);
  color: var(--color-gray-900);
  border-radius: $border-radius-lg;
  border: 1px solid var(--color-border);
  margin: map-get($spacers, 6) 0;
  overflow: hidden;
  
  [data-theme="dark"] & {
    background-color: #0d1117;
    color: #c9d1d9;
    border: none;
  }
  
  code {
    display: block;
    background-color: transparent;
    color: inherit;
    padding: map-get($spacers, 4);
    overflow-x: auto;
    font-family: 'JetBrains Mono', 'Fira Code', 'SF Mono', Monaco, 'Inconsolata', 'Fira Mono', monospace;
    font-size: 0.875rem;
    line-height: 1.7;
    font-weight: normal;
    
    [data-theme="dark"] & {
      color: #c9d1d9;
    }
    
    &::-webkit-scrollbar {
      height: 6px;
    }
    
    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.1);
    }
    
    &::-webkit-scrollbar-thumb {
      background: rgba(255, 255, 255, 0.3);
      border-radius: $border-radius-full;
      
      &:hover {
        background: rgba(255, 255, 255, 0.4);
      }
    }
  }
}

// Code block wrapper
.code-block {
  position: relative;
  margin: map-get($spacers, 6) 0;
  
  // Ensure no borders in dark mode
  [data-theme="dark"] & {
    border: none !important;
    
    pre {
      border: none !important;
    }
  }
  
  &[data-language]::before {
    content: attr(data-language);
    position: absolute;
    top: 0;
    right: 0;
    padding: map-get($spacers, 1) map-get($spacers, 3);
    font-size: map-get($font-sizes, 'xs');
    font-weight: map-get($font-weights, 'medium');
    color: var(--color-gray-400);
    text-transform: uppercase;
    letter-spacing: 0.05em;
    background-color: rgba(0, 0, 0, 0.2);
    border-bottom-left-radius: $border-radius;
    z-index: 5;
    
    [data-theme="dark"] & {
      background-color: rgba(0, 0, 0, 0.3);
    }
  }
}

// Copy button positioning and styling
.code-copy-button {
  position: absolute !important;
  top: map-get($spacers, 2) !important;
  right: map-get($spacers, 2) !important;
  padding: map-get($spacers, 1) map-get($spacers, 2);
  font-size: map-get($font-sizes, 'xs');
  font-weight: map-get($font-weights, 'medium');
  color: var(--color-gray-600);
  background-color: rgba(255, 255, 255, 0.9);
  border: 1px solid var(--color-border);
  border-radius: $border-radius;
  transition: all $transition-fast;
  opacity: 0;
  z-index: 10;
  
  [data-theme="dark"] & {
    color: var(--color-gray-400);
    background-color: rgba(30, 41, 59, 0.9);
    border-color: var(--color-gray-700);
  }
}

pre:hover .code-copy-button,
.highlight:hover .code-copy-button,
.code-block:hover .code-copy-button {
  opacity: 1;
}

// Copy button hover and copied states
.code-copy-button {
  &:hover {
    color: var(--color-primary-600);
    background-color: var(--color-primary-50);
    border-color: var(--color-primary-200);
    
    [data-theme="dark"] & {
      color: var(--color-primary-400);
      background-color: var(--color-gray-700);
      border-color: var(--color-gray-600);
    }
  }
  
  &.copied {
    color: var(--color-success);
    
    &::after {
      content: ' ✓';
    }
  }
}

// Remove extra Rouge copy button if it exists
.rouge-code .copy-to-clipboard-button {
  display: none;
}

// Fix line numbers container
.rouge-table {
  width: 100%;
  margin: 0;
  border-spacing: 0;
  background: transparent;
  
  td {
    padding: 0;
    vertical-align: top;
    border: none;
  }
  
  // Hide any copy buttons in the gutter
  .rouge-gutter {
    width: auto;
    min-width: 3em;
    padding-right: map-get($spacers, 3);
    text-align: right;
    background-color: transparent;
    user-select: none;
    
    pre {
      margin: 0;
      background: none;
      border: none;
      padding: map-get($spacers, 4) 0;
      color: var(--color-gray-500);
      
      [data-theme="dark"] & {
        color: var(--color-gray-600);
      }
    }
    
    // Hide any copy buttons in line numbers
    .code-copy-button,
    button {
      display: none !important;
    }
  }
  
  .rouge-code {
    padding-left: 0;
    width: 100%;
    
    pre {
      margin: 0;
      background: none;
      border: none;
      padding: map-get($spacers, 4) map-get($spacers, 4) map-get($spacers, 4) 0;
    }
  }
}

// Ensure highlight block doesn't add borders
.highlight {
  margin: map-get($spacers, 6) 0;
  background: transparent;
  border: none;
  position: relative;
  
  > pre {
    margin: 0;
  }
  
  // Override any borders in dark mode
  [data-theme="dark"] & {
    pre {
      border: none;
    }
  }
}

// Code blocks inside steps should follow theme
.step-content {
  pre {
    background-color: var(--color-gray-50);
    
    [data-theme="dark"] & {
      background-color: #0d1117;
      border: none;
    }
  }
  
  code {
    [data-theme="dark"] & {
      color: #c9d1d9;
    }
  }
}

